:root {
	--pj-bg: #253741;
	--pj-text: #92adbb;
	--pj-highlight: #ffffff;
	--pj-image-size: 800px;
	--pj-image-margin: -450px;
}

.projects {
	margin: 30px 0px;
}

.projects-content {
	background: var(--pj-bg);
	padding: 50px 0px;
	box-sizing: border-box;
	margin-bottom: 30px;
}

.projects-description {
	display: flex;
	align-items: center;
	height: 100%;
	width: 40%;
	padding: 0px 30px;
	box-sizing: border-box;
}

.projects-description h3 {
	font-size: var(--text-size);
	color: var(--pj-highlight);
}

.projects-description p {
	color: var(--pj-text);
}
.projects-description button,
.projects-nav button {
	background: none;
	border: none;
	outline: none;
	cursor: pointer;
}

.projects-description button {
	color: var(--pj-text);
	font-size: var(--text-size);
	margin: 0px;
	padding: 0px;
}

.projects-image {
	/* background: orange; */
	max-width: var(--pj-image-size);
	float: right;
	margin-top: var(--pj-image-margin);
}

.projects-image img {
	width: 100%;
}

.projects-nav button {
	font-size: var(--text-size);
	color: var(--text-color);
	margin-right: 10px;
}

.projects-nav button:hover {
	color: white;
}

@media (orientation: portrait) {
	.projects-image {
		float: none;
		margin: 0px;
		width: 100%;
	}

	.projects-description {
		width: 100%;
		padding: 30px;
		box-sizing: border-box;
	}

	.projects-content {
		padding: 0px;
	}

	.projects-nav {
		margin: auto;
		width: fit-content;
	}
}
